import * as React from 'react';
import clsx from 'clsx';
import { Box, Tab, Tabs, withStyles, WithStyles, AppBar } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Loadable from '@loadable/component';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';
import { Link as RouterLink } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { withRouter, RouteComponentProps } from 'react-router';
import { RootState } from '../../reducers';
import Style from './style';
import { Resources } from '../../constants/resources';
import { GetStatesAction } from '../../redux/PartnerWithUs/types';

const TabPanel = Loadable(() => import('../../components/TabPanel'));
const Supplier = Loadable(() => import('../../components/Supplier'));
const Landlord = Loadable(() => import('../../components/Landlord'));

interface Props extends WithStyles<typeof Style>, RouteComponentProps {
	getStates: typeof GetStatesAction.action;
}
interface State {
	selectedTab: number;
}
/**
 * PartnerWithUs page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class PartnerWithUs extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			selectedTab: 0
		};
	}

	componentDidMount() {
		// scrollIntoView(document.getElementById('partner') as any);
		const { getStates } = this.props;
		getStates();
	}

	handleTabChange = (event: React.ChangeEvent<any>, newValue: number) => {
		this.setState({ selectedTab: newValue });
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes } = this.props;
		const { selectedTab } = this.state;
		const { REACT_APP_CAPTACHA_KEY = '' } = process.env;
		return (
			<GoogleReCaptchaProvider reCaptchaKey={REACT_APP_CAPTACHA_KEY}>
				<div className={classes.content}>
					<span id="partner" />
					<div className={classes.block}>
						<div className={classes.largerFont}>{Resources.PartnerWithUsTitle}</div>
					</div>
					<AppBar
						position="static"
						color="default"
						className={classes.appBar}
						elevation={2}
					>
						<Tabs
							centered
							className={classes.tabs}
							onChange={this.handleTabChange}
							value={selectedTab}
							aria-label="simple tabs example"
							indicatorColor="primary"
							textColor="primary"
							variant="fullWidth"
							scrollButtons="auto"
							classes={{
								indicator: classes.indicator
							}}
							TabIndicatorProps={{ children: <span /> }}
						>
							<Tab
								color="primary"
								disableRipple
								label={
									<Box
										className={clsx(classes.tabFont, {
											[classes.activeTab]: selectedTab === 0
										})}
									>
										Suppliers
									</Box>
								}
							/>
							<Tab
								color="primary"
								disableRipple
								label={
									<Box
										className={clsx(classes.tabFont, {
											[classes.activeTab]: selectedTab === 1
										})}
									>
										Landlords
									</Box>
								}
							/>
						</Tabs>
					</AppBar>
					<TabPanel value={selectedTab} index={0}>
						<>
							<div className={clsx(classes.section, classes.smallFont)}>
								{Resources.PartnerWithUsSuppliersContent_1}
								<br />
								<br />
								{Resources.PartnerWithUsSuppliersContent_2}
								<br />
								<br />
								<div className={classes.note}>
									{Resources.PartnerWithUsSuppliersNote}
								</div>
								<br />
								<div className={classes.section}>
									{Resources.MandatoryFieldNote}
								</div>
							</div>
							<br />
							<div>
								<Supplier />
							</div>
						</>
					</TabPanel>
					<TabPanel value={selectedTab} index={1}>
						<>
							<div className={clsx(classes.section, classes.smallFont)}>
								{Resources.PartnerWithUsLandlordsContent_1}
								<br />
								<br />
								{Resources.PartnerWithUsLandlordsContent_2}
								<br />
								<br />
								<div className={classes.note}>
									{Resources.PartnerWithUsSuppliersNote}
								</div>
								<br />
								<div className={classes.section}>
									{Resources.MandatoryFieldNote}
								</div>
							</div>
							<div>
								<Landlord />
							</div>
						</>
					</TabPanel>
				</div>
			</GoogleReCaptchaProvider>
		);
	}
}

const mapStateToProps = (state: RootState) => ({});

const mapDispatchToProps = {
	getStates: GetStatesAction.action
};

export default withStyles(Style, { name: 'DmartCorpFreshPartnerWithUs' })(
	withRouter(connect(mapStateToProps, mapDispatchToProps)(PartnerWithUs))
);
